/**
 * @description
 * Creates a new URL by combining the specified URLs
 *
 * @example
 * ``` ts
 * combineUrls('https://api.github.com/', '/users');
 * // => 'https://api.github.com/users'
 * combineUrls('https://api.github.com/users', '');
 * // => 'https://api.github.com/users'
 * ```
 *
 * @param {string|function} baseURL The base URL
 * @param {string} relativeURL The relative URL
 * @returns {string} The combined URL
 *
 * @docsCategory preference/utils/uri
 * @codeFilter combine-urls
 */
export const combineUrls = (
  baseURL: string | (() => string),
  relativeURL: string
) => {
  const myBaseUrl = typeof baseURL === 'function' ? baseURL() : baseURL;
  return relativeURL
    ? myBaseUrl.replace(/\/+$/, '') + '/' + relativeURL.replace(/^\/+/, '')
    : myBaseUrl;
};
